.smartcharts--navigation-widget .home{display:none !important}
.smartcharts--has-markers .stx-subholder{z-index:108 !important}
.ciq-menu{position:relative}
.smartcharts-light .ciq-menu{color:rgba(0,0,0,0.8)}
.smartcharts-dark .ciq-menu{color:#7f8397}
.ciq-menu>span{padding:0 12px 0 0;position:relative;font-weight:400}
.ciq-menu .cq-menu-btn{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}
.ciq-menu.ciq-enabled>.cq-menu-btn{cursor:pointer}
.cq-menu-dropdown{display:block;line-height:2.1em;position:absolute;overflow:hidden;height:auto;border-radius:3px;bottom:5em;pointer-events:none;opacity:0;z-index:3;transition:opacity 300ms,-webkit-transform 300ms;transition:transform 300ms,opacity 300ms;transition:transform 300ms,opacity 300ms,-webkit-transform 300ms;box-shadow:0px 2px 4px rgba(0,0,0,0.2);margin-top:5px}
.smartcharts-light .cq-menu-dropdown{color:#7f8397;background-color:#fff}
.smartcharts-dark .cq-menu-dropdown{color:#7f8397;background-color:#191c31}
.cq-menu-dropdown.cq-menu-dropdown-enter-done,.cq-menu-dropdown.cq-menu-dropdown-enter-active{opacity:1}
.cq-menu-dropdown.cq-menu-dropdown-enter-done{pointer-events:initial}
.cq-menu-dropdown .title{display:inline-block;height:20px;line-height:20px;padding:1em 1.6em 0.5em;box-sizing:content-box}
.cq-menu-dropdown .title .title-text{display:inline-block;font-size:1.2em;font-weight:400}
.smartcharts-light .cq-menu-dropdown .title{color:#7f8397}
.smartcharts-dark .cq-menu-dropdown .title{color:#fff}
.smartcharts-light .cq-menu-dropdown .title>.ic-icon{color:#7f8397}
.smartcharts-dark .cq-menu-dropdown .title>.ic-icon{color:#7f8397}
.cq-menu-dropdown .icon-close-menu{cursor:pointer;position:absolute;top:5px;right:5px;height:30px;width:30px;pointer-events:none;opacity:0}
.smartcharts-light .cq-menu-dropdown .cq-categorical-display .cq-lookup-filters{background:#fff}
.smartcharts-dark .cq-menu-dropdown .cq-categorical-display .cq-lookup-filters{background:#101320}
.smartcharts-mobile .ciq-menu.stxMenuActive{z-index:9}
.smartcharts-mobile .ciq-menu .cq-menu-btn:hover>.ic-icon svg,.smartcharts-mobile .ciq-menu .cq-menu-btn:hover>.cq-toggle svg{background:transparent}
.smartcharts-mobile .cq-modal-dropdown{border:none;pointer-events:none;z-index:-1;position:fixed;height:inherit;width:100%;top:0}
.smartcharts-mobile .cq-modal-dropdown.stxMenuActive{z-index:2}
.smartcharts-mobile .cq-modal-dropdown.ciq-studies .cq-menu-overlay .cq-menu-dropdown,.smartcharts-mobile .cq-modal-dropdown.cq-comparison-new .cq-menu-overlay .cq-menu-dropdown,.smartcharts-mobile .cq-modal-dropdown.cq-chart-title .cq-menu-overlay .cq-menu-dropdown{height:100%}
.smartcharts-mobile .cq-modal-dropdown.ciq-studies .cq-menu-overlay .cq-menu-dropdown .title,.smartcharts-mobile .cq-modal-dropdown.cq-comparison-new .cq-menu-overlay .cq-menu-dropdown .title,.smartcharts-mobile .cq-modal-dropdown.cq-chart-title .cq-menu-overlay .cq-menu-dropdown .title{border-bottom:none !important}
.smartcharts-mobile .cq-modal-dropdown .cq-menu-overlay{position:relative;transition:opacity 300ms;background:rgba(0,0,0,0.3);height:inherit;width:100%;padding:0;opacity:0}
.smartcharts-mobile .cq-modal-dropdown.stxMenuActive .cq-menu-overlay{opacity:1;pointer-events:initial;visibility:visible}
.smartcharts-mobile .cq-modal-dropdown.cq-chart-title .cq-menu-dropdown{-webkit-transform:translateY(-3em);transform:translateY(-3em)}
.smartcharts-mobile .cq-modal-dropdown .cq-scroll-panel{border-left:none}
.smartcharts-mobile .cq-modal-dropdown .cq-menu-dropdown{height:auto;bottom:0;box-shadow:none;border-radius:0;width:100%;position:absolute;-webkit-transform:translateY(3em);transform:translateY(3em);border:none}
.smartcharts-mobile .cq-modal-dropdown .cq-menu-dropdown.cq-menu-dropdown-enter-done,.smartcharts-mobile .cq-modal-dropdown .cq-menu-dropdown.cq-menu-dropdown-enter-active{-webkit-transform:translateY(0em);transform:translateY(0em)}
.smartcharts-mobile .cq-modal-dropdown .cq-menu-dropdown .icon-close-menu{pointer-events:initial;opacity:1}
.cq-chart-unavailable{position:absolute;text-align:center;width:100%;top:50%;z-index:1;font-size:1.7em;color:#878787}
.chart-line{position:absolute;z-index:35}
.chart-line.horizontal{width:100%;left:0;font-size:1em}
.chart-line.horizontal:hover{z-index:105}
.chart-line.horizontal.draggable>.draggable-area{height:20px;top:-8px;position:absolute;width:100%}
.chart-line.horizontal.draggable>.draggable-area:hover{background:rgba(0,0,0,0.05)}
.chart-line.horizontal .drag-line{height:1px;float:left;width:100%;margin-left:-10px;position:relative;border-color:inherit;border-top-width:1px}
.draggable.chart-line:hover.horizontal{cursor:ns-resize}
.draggable.chart-line:hover.vertical{cursor:ew-resize}
.hide-pricelines>.chart-line.horizontal{display:none}
.drag-price{display:block;height:24px;top:-11px;position:absolute;right:-1px;border:1px solid inherit}
.no-touch .price-arrow span{position:absolute;top:4px;left:6px;display:block;width:15px;height:15px;background-position:-4px -488px;opacity:0;transition:opacity 0.2s}
.no-touch .chart-line:hover .price-arrow{width:20px;height:23px}
.no-touch .chart-line:hover .price-arrow span{opacity:1}
.price{font-size:12px;font-weight:bold;display:block;padding:3px 1px;line-height:18px;overflow:hidden;text-align:center}
.draggable .price{padding-left:16px}
.draggable .price:after{font-weight:normal;content:'≡';font-size:25px;position:absolute;left:1px;top:1px;-webkit-transform:scaleX(0.8);transform:scaleX(0.8)}
.shade.hidden{opacity:0}
.shade{position:absolute;width:100%;top:-120px;left:0;right:0;height:120px !important;opacity:0.3;transition:opacity 0.1s ease-out}
.top-shade{background-image:linear-gradient(to bottom, transparent, var(--shade-color))}
.between-shade{opacity:0.2;background-color:var(--shade-color)}
.bottom-shade{background-image:linear-gradient(to bottom, var(--shade-color), transparent)}
.cq-loader .cq-loader-spin,.cq-comparison-loader,.cq-loading{background-image:url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='32' height='32'%3E%3Cpath d='M16 0C7.163 0 0 7.163 0 16s7.163 16 16 16 16-7.163 16-16S24.837 0 16 0m0 4c6.627 0 12 5.373 12 12s-5.373 12-12 12S4 22.627 4 16 9.373 4 16 4' opacity='.25'/%3E%3Cpath d='M16 0c8.837 0 16 7.163 16 16h-4c0-6.627-5.373-12-12-12V0z'%3E%3CanimateTransform type='rotate' repeatCount='indefinite' calcMode='linear' to='360 16 16' attributeName='transform' dur='0.8s' from='0 16 16'/%3E%3C/path%3E%3C/svg%3E");background-size:cover}
.smartcharts-dark .cq-loader .cq-loader-spin,.smartcharts-dark .cq-comparison-loader,.smartcharts-dark .cq-loading{background-image:url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='32' height='32' fill='%23fff'%3E%3Cpath opacity='.25' d='M16 0a16 16 0 0 0 0 32 16 16 0 0 0 0-32m0 4a12 12 0 0 1 0 24 12 12 0 0 1 0-24'/%3E%3Cpath d='M16 0a16 16 0 0 1 16 16h-4A12 12 0 0 0 16 4z'%3E%3CanimateTransform attributeName='transform' type='rotate' from='0 16 16' to='360 16 16' dur='0.8s' repeatCount='indefinite'/%3E%3C/path%3E%3C/svg%3E")}
.cq-loader{display:none;position:absolute;height:100%;width:100%;top:0px;left:0px}
.cq-loader.show{display:block;z-index:9}
.cq-loader .cq-loader-spin{position:absolute;height:50px;width:50px;left:50%;top:50%;margin-left:-25px;margin-top:-45px;z-index:1}
.smartcharts-light .cq-loader .cq-loader-spin{stroke:#000}
.smartcharts-dark .cq-loader .cq-loader-spin{stroke:#fff}
.cq-loader .cq-loader-status{padding-top:5px;position:absolute;height:20px;width:200px;left:50%;top:50%;margin-left:-100px;margin-top:10px;text-align:center}
.smartcharts-light .cq-loader .cq-loader-status{color:#000}
.smartcharts-dark .cq-loader .cq-loader-status{color:#fff}
.cq-loading{height:12px;width:12px;display:inline-block;vertical-align:middle;background-size:contain;margin-bottom:2px;margin-left:1px}
.rightclick_study .mouseManageText{display:inline}
.rightclick_study .mouseDeleteText{display:none}
.rightclick_study .overlayEdit{margin-top:-4px;margin-left:5px;opacity:1}
.rightclick_study .overlayEdit.stx-btn span{border:none;background-position:-301px -26px;width:23px}
.ciq-sleeping .chartContainer{opacity:0.5}
